.i-lg-container{
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #000;
	overflow: hidden;
	z-index: 9999;
}
.i-lg-filter{
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: hidden;
	transition: 5s;
}
.i-lg-filter:hover{
	filter: hue-rotate(70deg);
}
.i-lg:nth-child(1){
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 0;
	height: 0;
	border-radius: 50%;
	animation: scaleOne 4s infinite alternate cubic-bezier(0, 0.47, 1, 1);
	background-image: linear-gradient(to right, rgba(255,0,0,0.3),rgba(255,255,0,0.3));
	z-index: 1;
}

@keyframes scaleOne{
	100%{
		width: 250px;
		height: 250px;
		border-radius: 50%;
		background-image: linear-gradient(to right, rgba(255,0,0,0.3),rgba(255,255,0,0.3));
		opacity: 0;
	}
}

.i-lg:nth-child(2){
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 0;
	height: 0;
	border-radius: 50%;
	animation: scaleTwo 3s infinite alternate cubic-bezier(0, 0.47, 1, 1);
	background-image: linear-gradient(to right, rgba(255,0,0,0.3) , rgba(255,255,0,0.3));
	z-index: 2;
}

@keyframes scaleTwo{
	100%{
		width: 150px;
		height: 150px;
		border-radius: 50%;
		background-image: linear-gradient(to right, rgba(255,0,0,0.3),rgba(255,255,0,0.3));
		opacity: 0;
	}
}
.i-lg:nth-child(3){
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 0;
	height: 0;
	border-radius: 50%;
	animation: scaleThree 2s infinite alternate cubic-bezier(0, 0.47, 1, 1);
	background-image: linear-gradient(to right, rgba(255,0,0,0.3) , rgba(255,255,0,0.3));
	z-index: 3;
}

@keyframes scaleThree{
	100%{
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-image: linear-gradient(to right, rgba(255,0,0,0.3),rgba(255,255,0,0.3));
		opacity: 0;
	}
}